import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import {FormGroup, FormControl, Validators } from '@angular/forms';

import { ContactPage } from '../contact/contact';

import { ServicesLoginProvider } from '../../providers/services-login/services-login';

import 'rxjs';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {

  loginPass:FormGroup;
  data = {};

  informes =[];



  constructor(public navCtrl: NavController,
              public _login:ServicesLoginProvider)
  {}

  sincronizar() {
    this._login.getInformes()
        .subscribe( (resp:any)=>{
          // console.log(resp)
          this.informes = resp;
          localStorage.setItem('informes', JSON.stringify(this.informes))
        })
  }

  goToMyPage(id) {
    // go to the MyPage component
    this.navCtrl.push('InfoClientePage', {
      'id': id
    });
  }

}



